.page {
  font-family: "Poppins", sans-serif;
  font-size: 16px;
  line-height: 140%;
  color: $text-main-color;
  background-color: $background-color;
  scroll-behavior: smooth;

  &:has(.page__menu:target) {
    overflow: hidden;
  }

  &__body {
    margin: 0;
    min-width: 320px;
  }

  &__menu {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    opacity: 0;
    pointer-events: none;

    transition: all 0.3s;
    transform: translateX(-100%);

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
      overflow: hidden;
    }
  }
}
